body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    height: 100vh;
    margin-top: 0px;
}

.main-weather {
    height: 100vh;
    background-image: linear-gradient(
        to bottom,
        #a044ff,
        #a251ff,
        #a45cff,
        #a666ff,
        #a970ff
    );
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1% 2% 1% 2%;
    margin-bottom: 0px;
}
#weather-section {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 88%;
    align-items: center;
    margin-top: 0px;
    padding: 0%;
}
.today-weather {
    margin-top: 0;
    visibility: hidden;
}
.next-five-days {
    width: 60%;
    height: auto;
    text-align: center;
    visibility: hidden;
    display: flex;
    justify-content: space-around;
}
button[name="home"] {
    all: unset;
    cursor: pointer;
}
.spinner {
    text-align: center;
    width: 50%;
    margin: 15% auto;
}

.hide-side-bar {
    visibility: hidden;
}
.side-bar {
    height: 97.8vh;
    width: 22%;
    position: absolute;
    top: 0%;
    left: 0%;
    background-color: #6ffe90;
    font-family: "Raleway", "sans-serif";
    padding-left: 2%;
    padding-top: 1%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
button[name="close"],
button[name="open"] {
    all: unset;
    cursor: pointer;
}
ul,
p,
span {
    list-style-type: none;
    font-size: 2em;
}
a,
h6 {
    text-decoration: none;
    color: #a970ff;
}
a:visited {
    text-decoration: none;
}
p {
    width: 20%;
    color: white;
}
ul {
    padding: 0;
}
.social-logos {
    width: 35%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.list {
    margin-left: 2%;
}

.today-weather {
    width: 50%;
    margin: 5% auto 3% auto;
    text-align: center;
}
h1 {
    font-family: "Spartan", sans-serif;
    color: #70ff91;
    font-size: 1em;
}
h2 {
    font-family: "Spartan", sans-serif;
    color: white;
    margin: 0;
}
h4 {
    font-family: "Oxygen", sans-serif;
    font-size: 1.3em;
    margin: 0;
}
h5 {
    margin: 0;
    font-size: 1.4em;
    color: white;
    font-family: "Oxygen", sans-serif;
}

