.search-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
input[type="checkbox"] {
    display: none;
}
label {
    width: 50%;
    margin: auto;
    color: #a970ff;
    background-color: blanchedalmond;
    padding: 1%;
    display: flex;
}
label:hover {
    cursor: pointer;
}
.search-box {
    width: 30%;
    height: 35px;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: #a970ff;
    box-shadow: #16161670 0px 5px 10px;
    padding-left: 2%;
    padding-right: 2%;
}
input[type="text"] {
    all: unset;
    text-align: left;
}
::-webkit-input-placeholder {
    color: rgba(216, 216, 216, 0.603);
}
:-ms-input-placeholder {
    color: rgba(216, 216, 216, 0.603);
}
::placeholder {
    color: rgba(216, 216, 216, 0.603);
}
button[name="search"] {
    border: none;
    cursor: pointer;
    background-color: #70ff91;
    padding: 1%;
    color: #a970ff;
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: #16161670 0px 5px 10px;
    font-family: "Raleway", "sans-serif";
    font-weight: bold;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
}
button[name="search"]:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
button[name="search"]:active {
    color: #70ff91;
    background-color: #a970ff;
}

.home-page {
    height: 100vh;
    background-image: linear-gradient(
        to top,
        #ff9472,
        #ff897a,
        #fe7f84,
        #fa7790,
        #f2709c
    );
}
.icon {
    text-align: center;
    width: 50%;
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-family: "Raleway", "sans-serif";
    color: white;
    visibility: hidden;
}
.search {
    visibility: hidden;
    width: 50%;
    margin: auto;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

h3 {
    font-family: "Raleway", "sans-serif";
    color: white;
    font-size: 0.95em;
}
.logo {
    width: 50%;
    margin: auto;
    text-align: center;
    padding-top: 1%;
}

